"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import {
  ResizableHandle,
  ResizablePanel,
  ResizablePanelGroup,
} from "@/components/ui/resizable";
import { Separator } from "@/components/ui/separator";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { TooltipProvider } from "@/components/ui/tooltip";
import { AccountSwitcher } from "./account-switcher";
import { MailList } from "./mail-list";
import { MailDisplay } from "./mail-display";
import { defaultLayout, defaultMails } from "../data";

interface MailProps {
  defaultLayout?: number[];
  defaultCollapsed?: boolean;
  navCollapsedSize: number;
}

export function Mail({
  defaultLayout = defaultLayout,
  defaultCollapsed = false,
  navCollapsedSize,
}: MailProps) {
  const [isCollapsed, setIsCollapsed] = React.useState(defaultCollapsed);
  const [mail] = React.useState<typeof defaultMails>(defaultMails);

  return (
    <TooltipProvider delayDuration={0}>
      <ResizablePanelGroup
        direction="horizontal"
        onLayout={(sizes: number[]) => {
          document.cookie = `react-resizable-panels:layout:mail=${JSON.stringify(
            sizes,
          )}`;
        }}
        className="h-full max-h-[800px] items-stretch"
      >
        <ResizablePanel
          defaultSize={defaultLayout[0]}
          collapsedSize={navCollapsedSize}
          collapsible={true}
          minSize={15}
          maxSize={40}
          onCollapse={() => {
            setIsCollapsed(true);
            document.cookie = `react-resizable-panels:collapsed=${JSON.stringify(
              true,
            )}`;
          }}
          onResize={() => {
            setIsCollapsed(false);
            document.cookie = `react-resizable-panels:collapsed=${JSON.stringify(
              false,
            )}`;
          }}
          className={cn(
            isCollapsed &&
              "min-w-[50px] transition-all duration-300 ease-in-out",
          )}
        >
          <div className="flex h-full flex-col">
            <div
              className={cn(
                "flex h-[52px] items-center justify-center",
                isCollapsed ? "h-[52px]" : "px-2",
              )}
            >
              <AccountSwitcher isCollapsed={isCollapsed} />
            </div>
            <Separator />
            <MailList items={mail} />
          </div>
        </ResizablePanel>
        <ResizableHandle withHandle />
        <ResizablePanel defaultSize={defaultLayout[1]} minSize={30}>
          <Tabs defaultValue="inbox" className="h-full">
            <TabsList className="h-12 w-full justify-start rounded-none border-b bg-background px-4">
              <TabsTrigger value="inbox" className="relative h-9">
                Inbox
                <span className="ml-2 rounded-full bg-primary px-2 text-xs text-primary-foreground">
                  128
                </span>
              </TabsTrigger>
              <TabsTrigger value="drafts" className="relative h-9">
                Drafts
                <span className="ml-2 rounded-full bg-primary px-2 text-xs text-primary-foreground">
                  9
                </span>
              </TabsTrigger>
              <TabsTrigger value="sent" className="relative h-9">
                Sent
              </TabsTrigger>
              <TabsTrigger value="junk" className="relative h-9">
                Junk
                <span className="ml-2 rounded-full bg-primary px-2 text-xs text-primary-foreground">
                  23
                </span>
              </TabsTrigger>
              <TabsTrigger value="trash" className="relative h-9">
                Trash
              </TabsTrigger>
            </TabsList>
            <TabsContent value="inbox" className="m-0">
              <MailDisplay mail={mail[0]} />
            </TabsContent>
            <TabsContent value="drafts" className="m-0">
              <MailDisplay mail={mail[1]} />
            </TabsContent>
            <TabsContent value="sent" className="m-0">
              <MailDisplay mail={mail[0]} />
            </TabsContent>
            <TabsContent value="junk" className="m-0">
              <MailDisplay mail={mail[1]} />
            </TabsContent>
            <TabsContent value="trash" className="m-0">
              <MailDisplay mail={mail[0]} />
            </TabsContent>
          </Tabs>
        </ResizablePanel>
        <ResizableHandle withHandle />
        <ResizablePanel defaultSize={defaultLayout[2]} minSize={30}>
          <MailDisplay mail={mail[0]} />
        </ResizablePanel>
      </ResizablePanelGroup>
    </TooltipProvider>
  );
}
